* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html,
body {
	scroll-behavior: smooth;
	width: 1px;
}

html {
	font-size: 62.5%;
}

body {
	font-size: 1.6rem;

	background-image: url(../assets/workbench/back.jpg);
	background-size: cover;
	// background-position: center;
	background-repeat: no-repeat;

	//Hide scrollbar
	-ms-overflow-style: none;
	&::-webkit-scrollbar {
		display: none;
	}
	scrollbar-width: none;

	user-select: none;
}
